@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.navbar {
  /* background: #40b0bd; */
  font-size: large;
  border-bottom: 1px solid #d3d3d3;
}

.nav-link {
  color: #00a6df !important;
  font-size: 20px;
  font-weight: 600;
}

.nav-link:hover {
  color: rgb(255, 255, 255);
}

.navbar .navbar-toggler-icon {
  background-image: url(/portfolio/static/media/menu-icon.3dee6bf5.png) !important;
}

.jumbotron {
  /* background: #40b0bd !important; */
  background-color: #fff !important;
  font-family: "Kanit", sans-serif;
  background-image: url(/portfolio/static/media/soft-engg.84477295.png),
    linear-gradient(
      152deg,
      #60bfdb,
      #e5bcbc,
      #f4f5f7,
      #ffffff,
      #ffffff,
      #ffffff,
      #ffffff,
      #ffffff
    );
  background-size: contain, cover;
  background-repeat: no-repeat;
  background-position: 92%;
  min-height: 400px;
}

.cloud-container {
  max-width: 100;
  min-height: 50%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  /* background-color: #40b0bd; */
  /* background: linear-gradient(to bottom, #40b0bd 1%, #40b0bd 100%); */
}

.cloud-container {
  /* background: url(../Assets/clouds.png); */
  /* background-size: contain;
  background-repeat: repeat;
  background-color: #41b0bf; */
  position: relative;
}

.cloud-container .main-div {
  /* width: fit-content; */
  display: contents;
}

.cloud-container .name {
  font-weight: 500;
  font-size: 55px;
  margin-bottom: 0;
  color: #00a6df;
  line-height: 70px;
  margin-top: 5rem;
}

.cloud-container .sub-name {
  color: #2e246b;
  font-size: 22px;
}

.cloud-container .outlined-white {
  background-color: #41b0bf;
  color: #fff;
  border-radius: 30px;
  padding: 15px 25px;
  border: 1px solid #fff;
  font-size: 14px;
  letter-spacing: 1px;
}

.jumbotron .scroll-link {
  background-color: #41b0bf;
  color: #fff;
  text-decoration: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.jumbotron .scroll-link:hover {
  background: #fff;
  color: #41b0bf;
  border: none;
}

.cloud-container .outlined-white:hover {
  background: #d3d3d3;
  /* color: #41b0bf; */
}

@media (max-width: 450px) {
  .container {
    width: 100%;
  }

  .jumbotron {
    width: 100%;
  }
  /* .cloud-container {
    width: 450px;
  } */

  #cloud-intro {
    background: none;
  }

  .cloud-container .name {
    /* color: #e8f1ed; */
    display: contents;
  }

  .cloud-container .sub-name {
    /* color: #e8f1ed; */
    font-size: 22px;
  }
}

@media (max-width: 848px) {
  .jumbotron {
    min-height: 500px;
    background-image: url(/portfolio/static/media/soft-engg.84477295.png);
  }
}

.figure{
    display: flex !important;
    padding-left: 96px;
    padding-right: 96px;
    padding-top: 30px;
    /* background: #f0f8ff; */
    margin: 0;
}

.profile-pic{
    border-radius: 50% !important;
    margin: 10px;
    width:250px !important;
    height:200px !important;
}

.figure .figure-caption{
    font-family: "Kanit", sans-serif;
    font-size: 20px;
    color: #838383;
}

i {
    font-size: 40px;
    color: #3b5999;
    padding: 15px;
}

.fa-envelope{
    color: #3b5999;  
}

.fa-linkedin{
    color: #0077B5;
}

.fa-github{
    color: rgb(179, 24, 96);
}

.fa-instagram{
    color: #e4405f;
}

.fa-whatsapp{
    color: #25D366;
}

@media (max-width: 450px) {
    .figure {
        display: block !important;
        padding-right: 0%;
        padding-left: 0%;
    }

    .profile-pic{
        border-radius: 50% !important;
        margin: 10px;
        width:200px !important;
        height:200px !important;
    }

    .social-links{
        display: inline-block;
    }
  }
#skills {
  /* background-color: antiquewhite; */
  padding-bottom: 3%;
  background-image: url(/portfolio/static/media/profile-bk.e10408b9.png);
}

.title {
  font-weight: 500;
  font-size: 35px;
  font-family: "Kanit", sans-serif;
  color: #1f9ac0;
  margin: 0;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-family: "Kanit", sans-serif;
  align-items: center;
  /* padding: 0% !important; */
}

.list-item {
  padding: 0;
}

.skills .card-wrapper {
  margin: 2em;
  border-radius: 12px;
  min-width: 120px;
  padding: 10px;
  background: #fff;
  position: relative;
  top: 0;
  transition: top ease 0.5s;
  border: 1px solid #d3d3d3
}

.skills .card-wrapper:hover {
  top: -10px;
  box-shadow: 1px 24px 38px -9px rgba(0, 0, 0, 0.94);
}

.skills p {
  font-family: "Kanit", sans-serif;
  font-size: 23px;
  color: #68c01f;
  margin: auto;
}

.container img {
  padding: 10px 15px 15px 15px;
}

.container .fa-html5 {
  color: #1f4ac0;
}

.container .fa-css3-alt {
  color: #1f4ac0bd;
}

.container .fa-js {
  color: #9acd32;
}

.container .fa-java {
  color: #ff0000;
}

.container .fa-react {
  color: rgba(0, 0, 0, 0.774);
}

@media (max-width: 450px) {
  .skills {
    display: block;
  }
}

.contact {
  padding: 3% 4% 0 4%;
}

.contact .head-text {
  font-family: "Kanit", sans-serif;
  font-size: 300%;
  color: #1105059f;
  font-weight: 500;
  margin: 0;
}

.contact .sub-text {
  font-family: "Kanit", sans-serif;
  font-size: 150%;
  color: #dd1515;
  margin: 0;
}

.details {
  font-family: "Kanit", sans-serif;
}

.container .sign {
  font-family: 'Sacramento', cursive;
  font-size: 50px;
  color: #41b0bf !important;
}

.container .fa-heart {
  font-size: 120%;
  padding: 1%;
  color: #dd1515;
}

.social-media {
  display: flex;
  padding: 0%;
  justify-content: center;
}

@media (max-width: 450px) {
  .contact .row {
    display: block;
  }

  .contact .container{
      padding: 0 !important;
  }
}

.contact {
  padding: 0 4% 0 4%;
}

